import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";

export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			scrollTop: false,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					page: 0,
					orderList: []
				},
				{
					state: 12,
					text: '已付款',
					page: 0,
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '已结算',
					page: 0,
					loadingType: 'more',
					orderList: []
				},
				{
					state: 13,
					text: '已失效',
					page: 0,
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},
	onPageScroll: function(e) {
		this.scrollTop = e.scrollTop > 200;
	},
	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = parseInt(options.state);




		if (options.state === 0) {
			this.loadData()
		}


	},
	methods: {
		loadMore() {
			let index = this.tabCurrentIndex;
			this.navList[index].page = this.navList[index].page + 1;
			this.loadData();
		},
		topScrollTap: function() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},
		toGoodsInfo: function(itemid) {
			let relation_id = this.$queue.getData("relation_id");
			if (relation_id) {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid + "&relation_id=" + relation_id,
				})
			} else {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid,
				})
			}
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			let page = navItem.page;
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}
			if (navItem.loadingType === 'noMore') {
				return;
			}

			navItem.loadingType = 'loading';
			let relation_id = this.$queue.getData("relation_id");
			if (relation_id && relation_id !== 'undefined') {
				this.$Request.getT("/order/list?relation_id=" + relation_id + "&tk_status=" + state + "&page=" + page + "&size=10")
					.then(res => {
						if (res.status === 0) {
							let list = res.data.content;
							let orderList = list.filter(item => {
								//添加不同状态下订单的表现形式
								item = Object.assign(item, this.orderStateExp(item.tk_status));
								return item
							});
							orderList.forEach(item => {
								item.image = "http:" + item.item_img;
								navItem.orderList.push(item);
							});
							if (res.data.totalElements === navItem.orderList.length) {
								//判断是否还有数据， 有改为 more， 没有改为noMore
								navItem.loadingType = 'noMore';
							} else {
								//判断是否还有数据， 有改为 more， 没有改为noMore
								navItem.loadingType = 'more';
							}
							this.$set(navItem, 'loaded', true);
						} else if (res.status === -102) {
							this.$queue.showToast(res.msg);
							this.$queue.logout();
							uni.navigateTo({
								url: '/pages/public/login'
							})
						} else {
							this.$queue.showToast(res.msg);
						}
					});
			} else {
				navItem.loadingType = 'noMore';
				this.$set(navItem, 'loaded', true);
			}


		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#FF6DB2';
			switch (+state) {
				case 12:
					stateTip = '已付款';
					break;
				case 14:
					stateTip = '已收货';
					break;
				case 3:
					stateTip = '已结算';
					break;
				case 4:
					stateTip = '维权退款';
					break;
				case 13:
					stateTip = '已失效';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
